import Vue from "vue";
import App from "./App";
import router from "./router";
import config from "./config.js";
Vue.config.productionTip = false;
console.log("config", config);
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  components: { App },
  template: "<App/>"
});



